@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.sidePanel {
    background-color: #212934;
    font-family: "Montserrat", sans-serif;
    color: white;
    height: 100%;
    width: 200px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    font-style: bold;
    box-shadow: 0px 0px 10px black;
    text-align: left;
}

h3 {
    margin: 10px 0;
    margin-top: 20px;
}

h6 {
	margin: 5px 0;
	text-transform: uppercase;
}

p {
	font-size: 14px;
	line-height: 21px;
}

.card-container {
	border-radius: 5px;
	color: #edeeef;
	padding-top: 30px;
	position: relative;
	width: 350px;
	max-width: 100%;
	text-align: center;
}

.card-container .round {
	border: 1px solid white;
	border-radius: 50%;
	padding: 7px;
	width: 150px;
	height: 150px;
}

.sidePanel p.fullLine {
    border-bottom: 1px solid white;
    margin-top: 30px;
    margin-bottom: 20px;
}

.sidePanel ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 200px;;
    position: fixed;
    overflow: auto;
}

.sidePanel li {
    display: block;
    color: white;
    padding-bottom: 15px;
    padding-top: 15px;
    font-size: 23px;
    padding-left: 35px;
    cursor: pointer;
}

.sidePanel li:hover {
    background-color: #3d3e48;
}

