.home {
    margin-left: 150px;
    text-align: center;
}

.home img {
    height: 250px;
    width: 250px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
}

.home .clickable {
    float: left;
    margin-left: 500px;
}
.projectBox {
	border-radius: 10px;
	margin: 10px 1.6%;
	overflow: hidden;
	position: relative;
    width: 30%;
    font-family: 'Roboto', sans-serif;
}

.projectOverview {
    position: relative;
	text-align: center;
	width: 100%;
	height: 100%;
}

.overview {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    object-fit: cover;
	-webkit-transition: -webkit-transform 200ms ease-in;
	transition: -webkit-transform 200ms ease-in;
	transition: transform 200ms ease-in;
	transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
	height: 50%;  	
	width: 60%;
    background-color: rgba(255, 255, 255, 0.85);
    border-radius: 10px;
    margin: 0px;
    padding: 10px;
	font-size: 10px;
	overflow: hidden;
}

.projectBox img {
	object-fit: cover;
	-webkit-transition: -webkit-transform 200ms ease-in;
	transition: -webkit-transform 200ms ease-in;
	transition: transform 200ms ease-in;
	transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
	height: 100%;  	
    width: 100%;
}

.projectContent {
	background-color: rgba(0, 0, 0, 0.5);
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	color: #fff;
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
	-webkit-transition: opacity 200ms ease-in-out;
	transition: opacity 200ms ease-in-out;
	width: 100%;
	height: 100%;
}

.projectBox:hover .projectContent {
	opacity: 1;
}

.projectBox:hover .overview {
	opacity: 0;
}

.projectBox:hover img {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.projectContent p {
	color: #fff;
	font-size: 15px;
	padding: 10px;
	text-align: center;
}

.projectBox p {
	line-height: 24px;
	margin: 0;
}

.projectContent a {
	background-color: #fff;
	border-radius: 3px;
	color: #000;
	margin-top: 20px;
	padding: 7px 15px;
	text-decoration: none;
}

@media (max-width: 960px) {
	.projectBox {
		width: 48%;
		margin: 15px 1%;
	}
}

@media (max-width: 768px) {
	.projectBox {
		width: 100%;
		margin: 15px 0;
	}
}

